body {
  background: #141414;
  color: #ffffff;
}

.container-fluid {
  display: 100%;
}

.movie-app > .row {
  overflow-x: auto;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.movie-app > .row::-webkit-scrollbar {
  display: none;
}

.image-container {
  position: relative;
  transition: transform 0.2s;
}

.image-container:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.image-container:hover .overlay {
  opacity: 1;
}

.overlay {
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  bottom: 0;
  font-size: 20px;
  padding: 20px;
  text-align: center;
}

.mr-2 {
  color: #ffffff;
}

